import { ITransaction } from '@/types/Transactions.type'
import { useMemo } from 'react'

interface IData {
  dataset: number
  color: string
}

const COLORS: Record<string, string> = {
  Products: '#E7FFD9',
  Transport: '#D9E6F2',
  Utilities: '#FFF9D9',
  CommunicationsAndInternet: '#D9F2E6',
  Entertainment: '#F2D9F2',
  Health: '#FFE1E1',
  Education: '#D9F0FF',
  Shopping: '#E7E1FF',
  Travel: '#F4EAD9',
  Subscriptions: '#E0E9FF',
  Gifts: '#FFD9C7',
  Income: '#D9FFD9',
  BonusesAndCashback: '#D9F2FF',
  Investments: '#FFF5D9',
  Other: '#F0F0F0',
  Send: '#F8D3E0',
}

export const useExpenseStatisticsCalculator = (
  transactions: ITransaction[]
) => {
  const EXPENSE_STATISTICS_DATA = useMemo(() => {
    const data: Record<string, IData> = {}
    transactions?.forEach((el) => {
      if (!data[el.category]) {
        data[el.category] = { dataset: el.amount, color: COLORS[el.category] }
      } else {
        data[el.category].dataset += el.amount
      }
    })

    console.log(data)
    const labels = Object.keys(data)
    const dataset = Object.values(data).map((el) => el.dataset)
    const colors = Object.values(data).map((el) => el.color)

    return {
      labels: labels,
      datasets: dataset,
      backgroundColor: colors,
      border: 6,
      cutout: '0',
      label: 'In a %',
      padding: 5,
    }
  }, [transactions])
  return { EXPENSE_STATISTICS_DATA }
}
